<template>
	<div class="me">
		<div class="my-hd">
			<div class="left">
				<img :src="$baseUrl + data.icon" class="im">
			</div>
			<router-link to='/login' class="username" tag='div'>{{data.nikename}}</router-link>
			<van-button type="warning" size="mini" :plain="true" @click="logout">注销</van-button>
		</div>
		<div class="main-t">
			<MeCell v-for="(item,index) of list" :key="index" :res="item" :idx = "index"></MeCell>
		</div>
		<Button txt="退出登录" ></Button>
	</div>
</template>

<script>
	import Vue from "vue"
	import MeCell from "../components/me-cell.vue";
	import Button from "../components/button.vue";
	import store from "../plugins/vuex.js"
	import {mapState} from 'vuex'
	export default {
		name:"me",
		data() {
			return {
				list:[
					{name:"会员中心"},
				{name:"我的订单"},
				{name:"优惠券"},
				{name:"我的票"},
				{name:"我的关注"},
				{name:"收货地址"},
				{name:"实名认证"},
				{name:"意见反馈"},
				{name:"客服电话"},
				],
				
			}
		},
		components: {
			MeCell,
			Button
		},
		mounted() {
		
		},
		updated() {

		},
		methods: {
			logout(){
				console.log(123)
				window.localStorage.removeItem('user');
				this.$store.commit('USER',{err:1})
				this.$router.push('/')
			}
		},
		//设置一个路由前置守卫,进入user页面之前判断是否已经登录了
		beforeRouteEnter(to,from,next){
			
			if(store.state.user.err == 0){
				console.log(store.state.user.err)
				next()
			}else{
				console.log(store.state.user)
				next('/login')
			}
		},
		computed:{
			...mapState({
				data:state=>state.user.data
			}),
		}
		
	}
</script>

<style scoped="scoped">
	.me{
		width: 320px;
		height: 620px;
	}
	.me .my-hd{
		height: 70px;
		background: #ff1268;
		vertical-align: middle;
		align-items: center;
		display: flex;
	}
	.me .my-hd .left{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background-color: #8492A6;
		margin-left: 10px;
	}
	.me .my-hd .left .im{
		width: 50px;
		height: 50px;
	}
	.me .my-hd .username{
		margin-left: 30px;
		font-size: 14px;
		line-height: .4rem;
		color: #FFFFFF;
	}
</style>
