<template>
	<div class="week"> 
		<ul  class="u">
			<li v-for="item of list"><a>{{item}}</a></li>
		</ul>
	</div>
</template>

<script>
	let date = new Date();
	let day =  date.getDay()+2;	
	let arr1 = [day,day+1,day+2,day+3]
	let days = day.toString();
	function chinanum(num){
	    var china = new Array('零','一','二','三','四','五','六');
	    var arr = new Array();
	    for(var i=0;i<num.length;i++){
	        arr[i] = china[num[i]];
	    }
	    return arr
	}
	let w = chinanum(arr1);
	w.push("周日");
	w.unshift("明天");
	w.unshift("今天");
	w[2]="周"+w[2];
	w[3]="周"+w[3];
	w[4]="周"+w[4];
	w[5]="周"+w[5];
	export default{
		data(){
			return{
			list:w,
			}
		},
		components:{},
		mounted() {
			
		},
		updated() {
			
		},
		methods:{
			
		}
	}
</script>

