//这里在引入axios
import axios from 'axios';

//这里正在引入服务
import {
	ask
} from './service.js';

import * as types from './types.js';

export default {
	[types.HOME]: ({
		commit,
		state
	},payload) =>{
		//注意此时this不再指向vue的实例
		ask({
			//接口名
			apiname:'home',
			params:{
				_limit:10,
				_page:1
			},
			//类型则是使用类型模块化的
			type:types.HOME,
			commit
		})
	},
	[types.FIND]: ({
		commit,
		state
	},payload) =>{
		//注意此时this不再指向vue的实例
		ask({
			//接口名
			apiname:'follow',
			params:{
				_limit:10,
				_page:1
			},
			//类型则是使用类型模块化的
			type:types.FIND,
			commit
		})
	},
	[types.DETAIL]: ({
	  commit,
	  state
	}, payload) => {
	  let {
	    _id,
	    dataName
	  } = payload;
	  ask({
	    apiname: 'detail',
	    type: types.DETAIL,
	    _id,
	    dataName,
	    commit
	  })
	
	},
	
	[types.USER]: ({
		commit,
		state
	},payload) => {
		let {
			username,
			password
		} = payload;
		let params = new URLSearchParams();
		params.append("username",username)
		params.append("password",password)
		return axios({
			url:`/api/login`,
			method:'post',
			data:params,
		}).then(
			res => {
				//将数据将给mutaions
				commit(types.USER,res.data)
				//将数据返回出去
				console.log(res)
				return res
			}
		)
	},
	
	addItem :({
		commit,
		state
	},payload) =>{
		console.log(payload)
		let arr = [...state.buycar];
		let f = false;
		arr.forEach((item,index) => {
			if(item._id == payload._id){
				item.number++;
				find = true;
			}
		});
		if(!f){
			payload.number = 1;
			arr.push(payload);
			console.log(arr)
		}
		commit('ADD_ITEM',arr)
	},
}