<template>
	<div class="reg">
		<img class="im" src="../assets/images/damai.png" />
		<div class="main">
			<!-- <van-uploader :after-read="afterRead" /> -->
			<van-uploader v-model="fileList" multiple :max-count="1" :after-read="afterRead"/>
			<AbInput txt="请输入账号" v-model="username"></AbInput>
			<AbInput txt="请输入密码" v-model="password"></AbInput>
			<AbInput txt="请输入您的昵称"></AbInput>
			<Button txt="同意并注册" @click.native="regest"></Button>
			<span class="spn">我已阅读接受《大麦网会员服务协议》及《隐私专项条款》并同意自动注册成为会员</span>
		</div>
	</div>
</template>

<script>
	import AbInput from "../components/ab-input.vue";
	import Button from "../components/button.vue"
	export default {
		data() {
			return {
				username: '',
				password: '',
				mess: '',
				fileList: []
			}
		},
		components: {
			AbInput,
			Button
		},
		mounted() {

		},
		updated() {

		},
		methods: {
			afterRead(fileList) {
				console.log(fileList.file)
				this.icon = fileList.file;
			},
			regest() {
				console.log(this.username, this.password, this.icon)
				let formData = new FormData();
				formData.append('username', this.username)
				formData.append('password', this.password)
				formData.append('icon', this.icon)
				console.log(this.icon)
				axios({
					url:'/api/reg',
					method:'post',
					data:formData
				}).then(
					res =>{
						if(res.data.err == 0){
							console.log(res.data);
							this.$router.push('/login')
						}else{
							this.mess = res.data.msg
						}
					}
				)
			}
		}
	}
</script>

<style scoped>
	.reg {
		text-align: center;
	}

	.reg .im {
		width: 86px;
		height: 39px;
		margin-top: 45px;
	}

	.reg .main {
		margin-top: 20px;
	}

	.reg .main .spn {
		display: block;
		margin-top: 60px;
		/* margin-left: 210px; */
		font-size: 12px;
		color: #999999;
	}
</style>
