<template>
	<div class="me-cell">
		<div class="title">
			<i :class="setClass(idx)"></i>{{res.name}}
		</div>
		<div class="body">
			<span>&gt;</span>
		</div>
	</div>
</template>

<script>
	export const type = {

		1: 'iconfont iconhuiyuan',
		2: 'iconfont icondingdan',
		3: 'iconfont iconyouhuiquan',
		4: 'iconfont iconwode-piaoquan',
		5: 'iconfont iconwodeguanzhu',
		6: 'iconfont iconshouhuodizhi',
		7: 'iconfont iconshimingrenzheng',
		8: 'iconfont iconyijianfankui',
		9: 'iconfont iconkefudianhua',
	}
	export default {
		props: {
			res: {
				type: Object,
				default: function() {
					return {}
				}
			},
			idx: {
				type: Number,
				default: 0
			}

		},
		data() {
			return {
				objClass: {
					iconfont: true,
					iconhuiyuan: true,
				}
			}
		},
		components: {},
		mounted() {

		},
		updated() {

		},
		methods: {
			setClass(idx) {
				let obj = {};
				obj[type[idx + 1]] = true;
				return obj;
			}
		}
	}
</script>

<style scoped="scoped">
	.me-cell {
		height: 50px;
		border-bottom: 1px solid #e7e7e7;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.me-cell .title {
		color: #666666;
		font-size: 14px;
	}

	.me-cell .title i {
		padding-right: 4px;
	}

	.me-cell .body {
		color: #666666;
		margin-right: 10px;
	}
</style>
