//这里正在使用对象的解构
import {
	HOME,TOTAL,FIND,TICKET,
	VIEW_LOADING,VIEW_NAV,VIEW_FOOT,
	DETAIL,USER
} from './types.js';

//这里正在修改state里面的数据
export default {
	[HOME]:(state,payload)=>state.home=payload,
	[TOTAL]:(state,payload)=>state.total=payload,
	[FIND]:(state,payload)=>state.find=payload,
	[TICKET]:(state,payload)=>state.ticket=payload,
	[VIEW_LOADING]:(state,payload)=>state.bLoading=payload,
	[VIEW_NAV]:(state,payload)=>state.bNav=payload,
	[VIEW_FOOT]:(state,payload)=>state.bFoot=payload,
	[DETAIL]:(state,payload)=>state.detail=payload,
	[USER]:(state,payload)=>state.user=payload,
	
	ADD_ITEM:(state,payload)=>{
		state.buycar = payload;
	}
}