<template>
	<div class="nav"> 
		<span><i class="iconfont iconlocation"></i>全国</span>
		<!-- <input type="text" id="ipt" placeholder="搜索明星,演出比赛,场馆"/> -->
		<van-search
		  v-model="value"
		  placeholder="请输入搜索关键词"
		  show-action
		  shape="round"
		>
		  <div slot="action" @click="onCancel">搜索</div>
		</van-search>
	</div>
</template>

<script>
	import {mapState} from 'vuex';
	export default{
		data(){
			return{
				value:''
			}
		},
		components:{},
		mounted() {
			
		},
		updated() {
			
		},
		methods:{
			onCancel(){
				this.home.forEach((item,index)=>{
					if(item.name.includes(this.value)){
						this.$router.push('/detail/'+item._id+'?dataName=home')
					}
				})
			}
		},
		computed:{
			...mapState(['home'])
		}
	}
</script>

