<template>
	<div class="login">
		<img class="im" src="../assets/images/damai.png" />
		<div class="main">
			<AbInput txt="请输入账号" v-model="username"></AbInput>
			<AbInput txt="请输入密码" v-model="password"></AbInput>
			<Button txt="登录" @click.native='denglu'></Button>
			<router-link class="spn" tag="span" to="/reg">新用户注册</router-link>
		</div>
	</div>
</template>

<script>
	import AbInput from "../components/ab-input.vue";
	import Button from "../components/button.vue"
	export default {
		data() {
			return {
				username: '',
				password: '',
				mess: ''
			}
		},
		components: {
			AbInput,
			Button
		},
		mounted() {

		},
		updated() {

		},
		methods: {
			denglu() {
				console.log(this.username, this.password)
				//这里正在使用$store身上的dispatch向actions中发起请求
				//因为这里没有引入类型模块化,所以不行使用模块化,但是可以手写
				this.$store.dispatch('USER', {
					username: this.username,
					password: this.password,
				}).then(
					res => {
						//根据返回的错误值判断是否跳转
						if (res.data.err == 0) {
							console.log(1);
							//这里正在使用编程式跳转到用户页面
							this.$router.push('/user');
							//这里正在将读回来的数据同步到localStorage
							window.localStorage.setItem('user', JSON.stringify(res.data))
						} else {
							this.mess = res.data.msg
							
						}
					}
				)
			}
		},
	}
</script>

<style scoped>
	.login {
		text-align: center;
	}

	.login .im {
		width: 86px;
		height: 39px;
		margin-top: 45px;
	}

	.login .main {
		margin-top: 20px;
	}

	.login .main .spn {
		display: block;
		margin-top: 20px;
		margin-left: 210px;
		font-size: 12px;
		color: #999999;
	}
</style>
