<template>
  <div id="app">
	<Header v-show="bNav"></Header>
	<router-view></router-view>
	<Footer v-show="bFoot"></Footer>
  </div>
</template>
<script>
	import Header from './header.vue';
	import Home from '../pages/home.vue';
	import Footer from './footer.vue';
	//这里在引入类型模块化
	import * as types from '../store/types.js';
	//这里在引入vuex的mapState方法,可以让组件直接修改mapState数据
	import {mapState} from 'vuex';
	export default {
		name:"App",
		components:{
			Header,
			Footer,
			Home,
		},
		mounted() {
			
		},
		//mapState代替的是计算属性computed
		computed:mapState([
			'bNav','bFoot'
		]),
		//这里正在进行路由监听|属性检测|数据观测
		watch:{
			$route:{
				handler(nextValue,PrevValue){
					//根据路由路径的变化来判断Nav和Foot
					let path = nextValue.path;
					//这里通过修改类型模块化的数据修改state
					if(/login|reg/.test(path)){
						this.$store.commit(types.VIEW_NAV,false)
						this.$store.commit(types.VIEW_FOOT,false)
					}
					if(path.includes('home')){
						this.$store.commit(types.VIEW_NAV,true)
						this.$store.commit(types.VIEW_FOOT,true)
					}
					if(/total|find|user|ticket|detail/.test(path)){
						this.$store.commit(types.VIEW_NAV,false)
						this.$store.commit(types.VIEW_FOOT,true)
					}
				},
				immediate:true
			}
		}
	}
</script>
<style>
	body > div#blockbyte-bs-indicator.blockbyte-bs-visible {
		display: none;
	}
	
</style>
