//这里是做好发出去的服务
import axios from 'axios';
import router from '../plugins/router.js';
console.log(router)
//通用服务
function ask ({
	//接口名
	apiname,
	//数据
	params,
	//要发出去的类型
	type,
	//通讯工具
	commit,
	//数据库中所需要的id
	_id = null,
	//拿详情是拿哪个数据库中的
	dataName = null,
}) {
	let url = _id ? '/api/' + dataName + '/' + _id : '/api/' + apiname;
	axios({
		url,
		params
	}).then(
		res => {
			//这里正在判断有没有登录,若没有登录则跳转到登录页面
			if(res.data.err === 1){
				//这里正在使用路由编程式跳转
				router.push('/login')
			} else {
				//这里是将数据发送给mutation,由mutation修改state的值
				commit(type,res.data.data)
			}
		}
	)
}

//这里将函数暴露出去
export {
	ask
}